.style_card__7K4_N{
    display: inline-block;
    text-align: center;
    background: white;
    border-radius: 10px;
    overflow: hidden;
    margin: 50px;
    box-shadow: 0px 6px 14px -5px #575757;
}

.style_card__7K4_N > .style_img_holder__jdYFj{
    width: 100%;
    position: relative;
    overflow: hidden;
    -webkit-clip-path: ellipse(150% 98% at 50% 0%);
            clip-path: ellipse(150% 98% at 50% 0%);
}

.style_card__7K4_N > .style_img_holder__jdYFj > img{
    width: 100%;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.style_card__7K4_N > .style_img_holder__jdYFj > div{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background: #ffffff00;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.style_card__7K4_N > .style_img_holder__jdYFj:hover > div{
    background: #ffffffc0;
}

.style_card__7K4_N > .style_img_holder__jdYFj:hover > img{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.style_card__7K4_N > .style_img_holder__jdYFj > div > .style_source__2Auax{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -webkit-transition: 0.3s;
    transition: 0.3s;
    opacity: 0;
}

.style_card__7K4_N > .style_img_holder__jdYFj:hover > div > .style_source__2Auax{
    opacity: 1;
}

.style_card__7K4_N .style_source__2Auax > i{
    color: #044089;
    font-size: 8em;
    display: inline-block;
    margin-bottom: 10px;
}

.style_card__7K4_N .style_source__2Auax > a{
    background: transparent;
    color: #044089;
    font-size: 1.2em;
    font-family: Josefin Sans;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
    display: inline-block;
    padding: 10px 20px 7px;
    border-style: solid;
    border-width: 2px;
    border-color: #044089;
    border-radius: 40px;
    cursor: pointer;
    -webkit-transition: 0.3s ease-in-out;
    transition: 0.3s ease-in-out;
}

.style_card__7K4_N .style_source__2Auax > a:hover{
    background: #044089;
    color: #ffffff;
}

.style_card__7K4_N h3{
    display: block;
    width: -moz-fit-content;
    margin: 10px 20px 0px;
    color: #044089;
    font-size: 1.3em;
    font-family: Josefin Sans;
    font-weight: 400;
    /* text-transform: uppercase; */
}

.style_card__7K4_N > span{
    display: block;
    width: calc(100% - 40px);
    padding: 20px;
    padding-top: 5px;
    text-align: justify;
    font-size: 1em;
    font-family: 'Montserrat';
}

@media (max-width: 500px){
    .style_card__7K4_N{
