.home{
  width: 100%;
  height: 100vh;
}

.home .overlay{
  top: 50%;
  width: 100%;
  height: 50%;
  background: linear-gradient(#5050fcf2, #8c43ffff);
  background: linear-gradient(#1e2b3b77, #1e2b3bf0);
  position: relative;
  font-family: 'Montserrat';
  /* clip-path: polygon(75% 0%, 100% 0%, 100% 100%, 25% 100%); */
  width: 100%;
}

.home header{
  display: block;
  width: 100%;
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;
}

.home .display-picture{
  background-size: cover;
  width: 300px;
  height:  300px;
  display: inline-block;
  border-radius: 50%;
  border-width: 5px;
  border-style: solid;
  margin: 25px;
}

.home h1, .home h3{
  margin: 0px;
}

.home h3{
  font-size: 1.4em;
  font-weight: 100;
}

.home h1{
  font-size: 4em;
  font-weight: 500;
}

@media (max-width: 500px){
  .home h1{
    font-size: 2.2em;
  }
}