.card{
    display: inline-block;
    text-align: center;
    background: white;
    border-radius: 10px;
    overflow: hidden;
    margin: 50px;
    box-shadow: 0px 6px 14px -5px #575757;
}

.card > .img_holder{
    width: 100%;
    position: relative;
    overflow: hidden;
    clip-path: ellipse(150% 98% at 50% 0%);
}

.card > .img_holder > img{
    width: 100%;
    transition: 0.3s;
}

.card > .img_holder > div{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background: #ffffff00;
    transition: 0.3s;
}

.card > .img_holder:hover > div{
    background: #ffffffc0;
}

.card > .img_holder:hover > img{
    transform: scale(1.3);
}

.card > .img_holder > div > .source{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: 0.3s;
    opacity: 0;
}

.card > .img_holder:hover > div > .source{
    opacity: 1;
}

.card .source > i{
    color: #044089;
    font-size: 8em;
    display: inline-block;
    margin-bottom: 10px;
}

.card .source > a{
    background: transparent;
    color: #044089;
    font-size: 1.2em;
    font-family: Josefin Sans;
    font-weight: 400;
    text-transform: uppercase;
    text-decoration: none;
    display: inline-block;
    padding: 10px 20px 7px;
    border-style: solid;
    border-width: 2px;
    border-color: #044089;
    border-radius: 40px;
    cursor: pointer;
    transition: 0.3s ease-in-out;
}

.card .source > a:hover{
    background: #044089;
    color: #ffffff;
}

.card h3{
    display: block;
    width: -moz-fit-content;
    margin: 10px 20px 0px;
    color: #044089;
    font-size: 1.3em;
    font-family: Josefin Sans;
    font-weight: 400;
    /* text-transform: uppercase; */
}

.card > span{
    display: block;
    width: calc(100% - 40px);
    padding: 20px;
    padding-top: 5px;
    text-align: justify;
    font-size: 1em;
    font-family: 'Montserrat';
}

@media (max-width: 500px){
    .card{
        margin: 10px;
    }

    .card > .img_holder > div > .source{
        width: 100%;
    }

    .card .source > a{
        width: 60%;
    }
}