.socialCard{
    width: 100%;
    max-width: 600px;
    border-radius: 20px;
    position: relative;
    height: 300px;
    border-radius: 20px;
    overflow: hidden;
    border: solid 5px #044089;
}

.socialCard .desc{
    box-sizing: border-box;
    font-family: Montserrat;
    width: 100%;
    height: 300px;
    background: linear-gradient( #1e2b3bf0, #1e2b3bf0), url('/bg.png') fixed;
    clip-path: polygon(60% 0%, 100% 0%, 100% 100%, 40% 100%);
    text-align: right;
    color: white;
    padding: 10px 20px;
}

.title{
    font-size: 40px;
    /* font-weight: 900; */
}

.subtitle{
    font-size: 12px;
    /* font-weight: 900; */
}

.text{
    display: inline-block;
    width: 44%;
    text-align: justify;
    text-align-last: right;
    font-size: 15px;
}

.img{
    position: absolute;
    top: 0px;
    left: 0px;
    width: 60%;
    height: 100%;
    background-size: cover;
    background-position: center;
}