.stack{
    width: 100%;
    color: #1e2b3b;
    min-height: 100vh;
    background: white;
}

.container{
    padding:0px 0px 200px;
    text-align: center;
    width: 100%;
    /* height: 100vh; */
    background: white;
    /* margin-top: 200px; */
    /* border-radius: 10px; */
}

.container .title{
    width: 100%;
    height: 35px;
    /* background: #8c43ff; */
    background: linear-gradient( #1e2b3bf0, #1e2b3bf0), url('/bg.png');
    background-attachment: fixed;
    position: relative;
    margin-bottom: 150px;
}

.container h1{
    display: inline-block;
    font-size: 2.5em;
    margin: 0px;
    font-family: 'Raleway';
    font-weight: 900;
    color: #044089;
    padding: 10px 50px;
    background: white;
    border-radius: 10px;
}

.cards{
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    margin-top: 200px;
}

@media (max-width: 500px){

    .container{
        padding-bottom: 100px;
        text-align: center;
    }

    .cards{
        margin: auto;
        width: 90%;
        grid-template-columns: 1fr;
        justify-items: center;
        margin-top: 200px;
        grid-row-gap: 20px;
    }

}